<template>
  <div>
    <el-scrollbar class="pepoleMessage">
      <ul class="messageBox">
        <li class="mag" v-for="(comment,index) in comments" :key="index">
          <img class="img" :src="comment.headerimageurl" alt/>
          <div class="commont">
            <p class="topFont">
              {{comment.name | nameIsNull}} <span style="display: inline-block;font-size: 14px">发表于：{{parseInt(comment.date) | timeIs }}</span>
            </p>
            <p class="bottomFont">{{comment.comment}}</p>
          </div>
        </li>
      </ul>
    </el-scrollbar>
    <div class="messageSenBox">
      <el-button @click="showClink" class="senMessage">写新留言</el-button>
    </div>


    <MessagePlan :isShow="isShow" :closeWindow="showClink"/>
  </div>
</template>

<script>
  import timeNow from "../../../../config/timeNow";
  import MessagePlan from "../../../../components/message/MessagePlan";

  export default {
    name: "Pepolemessage",
    data() {
      return {
        isShow: false
      };
    },
    components: {
      MessagePlan
    },
    methods: {
      showClink() {
        if(this.isShow){
          this.isShow = false
        }else {
          this.isShow = true;
        }
      }
    },
    computed: {
      comments() {
        return this.$store.state.comments;
      }
    },
    filters: {
      timeIs(value) {
        return timeNow(value, false);
      },
      nameIsNull(value) {
        if (value) return value;
        else return "匿名";
      }
    }
  };
</script>

