<template>
  <div class="right-chart-2">
    <div class="rc1-header">留言详情</div>
    <div class="rc1-chart-container">
      <div class="left">
        <div class="number">{{messageCount}}</div>
        <div>留言总数</div>
      </div>
      <dv-active-ring-chart class="right" :config="options"/>
    </div>
  </div>
</template>

<script>
  import {mapGetters} from "vuex";

  export default {
    name: "RightChart2",
    computed: {
      ...mapGetters(["messageCount"]),
      ...mapGetters(["showMan"]),
      options() {
        if (this.showMan.length === 0) {
          return;
        }
        return {
          data: [
            ...this.showMan
          ],digitalFlopStyle: {
            fontSize: 16
          }
        };
      }
    },
    data() {
      return {};
    },
  };
</script>

